const PostTemplate: FunctionComponent<PostTemplateProps> = function ({
   data: {
    allMarkdownRemark: { edges },
  },
}) {
  const {
    node: {
      html,
      frontmatter: {
        title,
        startDate,
        endDate,
        skills,
        contentImg: {
          publicURL
        },
        link
      },
    },
  } = edges[0]
// const PostTemplate: FunctionComponent<PostTemplateProps> = function (props) {
//   console.log(props)

  return (
  <Template>
     <TemplateBg>
        <div className='container'>
            
            <PostWrap>
           
           
              <GoBack to={'/'}><AiOutlineSwapLeft size={28}/></GoBack>
              <Title>{title}</Title>
              <div className='info-list'>
                <InfoList>
                  <InfoLine><AiOutlineTeam size={22}/><b>소속</b><InfoBox> 프리랜서</InfoBox></InfoLine>
                  <InfoLine><AiOutlineCalendar size={22}/><b>투입기간</b><InfoBox> {startDate} ~ {endDate}</InfoBox></InfoLine>
                  <InfoLine><AiOutlineUnorderedList size={22}/> 
                  <b>사용언어</b>
                  <LabelWrap>
                  {
                    skills.map( skill => <Label>{skill}</Label>)
                  }
                  </LabelWrap>
                   
                  </InfoLine>
                  <InfoLine> <AiOutlineLink size={22}/> <b>URL</b> {link?<InfoBox><a href={link} target={'_blank'}>{link}</a> </InfoBox>: '-'}</InfoLine>
                  
                </InfoList>
              </div>
             
                <ContentImg src={publicURL}/> 
              
                <div className='post-content'>
                  <PostContent html={html} />
                </div>
              
            
            
            </PostWrap>
            </div>
            </TemplateBg>
        </Template>
    )
}

export default PostTemplate


export type PostPageItemType = {
  node: {
    html: string
    frontmatter: PostFrontmatterType
  }
}
export const queryMarkdownDataBySlug = graphql`
  query queryMarkdownDataBySlug($slug: String) {
    allMarkdownRemark(filter: { fields: { slug: { eq: $slug } } }) {
      edges {
        node {
          html
          frontmatter {
            title
            startDate
            endDate
            skills
            contentImg {
              publicURL
            }
            link
           
          }
        }
      }
    }
  }
`